  letter-spacing: 1px;
  color: #000000;
  opacity: 1;
}
a{
  text-decoration: none !important;
  color: #000000 ;
}
a:hover{
  text-decoration: underline !important;
  color: #000000 ;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
  text-align: left;
  font: normal normal normal 20px/30px Poppins;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
  width: 270px;
}
.dropdown:hover .dropdown-content{
  display: block;
}
